.category_wrapper{
   padding-top: 30px;
   width: 200px;
   display: flex;
   flex-direction: column;
   align-items: center;
   background-color: rgb(230, 230, 230);
}
.item{
   width: 180px;
   height: 50px;
   border-radius: 15px;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   padding: 0 15px;
   font-size: 24px;
   line-height: 28px;
   color: #000000;
   font-weight: 400;
   background-color: rgb(214, 214, 214);
   cursor: pointer;
}

.item:not(:first-child) {
   margin-top: 20px;
}

.item:hover {
   background: rgb(184, 184, 184);
}

.item:active {
   background: rgb(131, 130, 130);
}

.like {
   color: rgb(224, 139, 139);
   margin-left: auto;
}
